<template>
  <!-- 登陆 -->
  <div class="login">
    <div class="login-title">
      <div class="luckc" @click="luckcLogo">
        <span class="luckc-text"> 随幸咖啡</span>
      </div>
      <div class="close" @click="menu">游客模式</div>
    </div>

    <div class="login-good">
      <!-- <div class="good-text">幸运的你,</div>
      <div class="good-text">今天有什么好事发生吗？</div>
      <div class="good-entext">Lucky you,</div>
      <div class="good-entext">Did anything good happen to you today?</div> -->
        <img @click="luckcLogo" class="auto-img luckc-logo" src="../assets/lucklogo.png" alt="" />

    </div>
    <van-form>
      <van-field
        v-model="userInfo.phone"
        name="手机号"
        label="手机号"
        placeholder="11位手机号"
      />
      <van-field
        v-model="userInfo.password"
        type="password"
        name="密码"
        label="密码"
        placeholder="6-16个字符"
      />
      <div class="btn-box">
        <van-button round block type="info" native-type="submit" @click="login"
          >登陆</van-button
        >
        <van-button
          class="register"
          round
          block
          type="default"
          native-type="submit"
          @click="isregister"
          >还没有账号吗？快来注册吧</van-button
        >
      </div>
      <p class="pwd">
        <span @click="goPage('Email')">忘记密码？</span>
      </p>
    </van-form>

    <van-action-sheet v-model="isShow" title="注册">
      <van-form>
        <van-field
          v-model="userRegInfo.nickName"
          name="用户名"
          label="用户名"
          placeholder="1-8个字符"
        />
        <van-field
          v-model="userRegInfo.phone"
          type="text"
          name="手机号"
          label="手机号"
          placeholder="11位手机号"
        />
        <van-field
          v-model="userRegInfo.password"
          type="password"
          name="密码"
          label="密码"
          placeholder="6-16个字符"
        />
        <div style="margin: 16px" class="btn-box">
          <van-button
            round
            block
            type="info"
            native-type="submit"
            @click="register"
            >注册</van-button
          >
        </div>
      </van-form>
    </van-action-sheet>
  </div>
</template>
<script>
import { valid } from "../utils/valid";
import { utils } from "../utils/utils";
export default {
  name: "Login",
  data() {
    return {
      isShow: false,
    };
  },
  created() {
    //置空数据
    this.$store.commit("loginModule/emptyData");
  },
  computed: {
    userInfo() {
      return this.$store.state.loginModule.userInfo;
    },
    userRegInfo() {
      return this.$store.state.loginModule.userRegInfo;
    },
  },

  methods: {
    //登陆
    login() {
      if (valid.validForm(this.userInfo, this)) {
        //加载提示
        this.$toast.loading({
          forbidClick: true,
          duration: 0,
          message: "正在登陆...",
        });

        //登陆请求
        this.axios({
          method: "POST",
          url: "/login",
          data: {
            appkey: this.appkey,
            phone: this.userInfo.phone,
            password: this.userInfo.password,
          },
          transformRequest: utils.transformRequest,
        })
          .then((d) => {
            //关闭加载提示
            this.$toast.clear();

            //如果登陆成功，将token保存在cookie中
            if (d.data.code == 200) {
              //将token分段储存在tokenData
              // let tokenData=d.data.token.split('.');
              //设置cookie过期时间为30天
              // let expires=30*24*60*60;

              // tokenData.forEach((v,i)=>{
              //   //v:为一个token分段，有三个分段
              //   //'t'+i为键名，v为值，expires为过期时间，时间以秒为单位
              //   this.$cookies.set('t'+i,v,expires)
              // })

              //存储token字符串
              localStorage.setItem("_t", d.data.token);

              //跳转页面
              this.$store.commit("changeData", { key: "isLogin", value: true });

              this.$router.push({ name: "Advertising" });
            } else {
              this.$toast(d.data.msg);
            }
          })
          .catch((err) => {
            this.$toast.clear();
          });
      }
    },

    //跳转注册
    register() {
      if (valid.validForm(this.userRegInfo, this)) {
        //ajax
        //加载提示
        this.$toast.loading({
          forbidClick: true,
          duration: 0,
          message: "注册中...",
        });
        this.axios({
          method: "POST",
          url: "/register",
          data: {
            appkey: this.appkey,
            nickName: this.userRegInfo.nickName,
            phone: this.userRegInfo.phone,
            password: this.userRegInfo.password,
          },
          //序列化请求体参数
          transformRequest: utils.transformRequest,
        })
          .then((d) => {
            //关闭注册加载提示
            this.$toast.clear();

            //如果注册成功
            if (d.data.code == 100) {
              //成功登陆，跳转登陆组建
              this.isShow = false;
            } else {
              this.$toast({ message: d.data.msg });
            }
          })
          .catch((err) => {});
      }
    },

    isregister() {
      this.isShow = true;
    },

    menu() {
      localStorage.removeItem("_t");
      this.$router.push({ name: "Menu" });
    },

    luckcLogo() {
      this.$toast("你发现了一个幸运logo");
    },
    //跳转
    goPage(item) {
      this.$router.push({ name: item });
    },
  },
};
</script>
<style lang="less" scoped>
.login {
  position: relative;
  padding-top: 30px;
  .login-title {
    border-bottom: 1px solid #d8d8d8;
    margin-top: 10px;
  }
  .luckc {
    position: absolute;
    top: 10px;
    left: 10px;
    height: 24px;
    display: flex;
    color: #1296db;
    
    .luckc-text {
      font-size: 18px;
      margin-left: 5px;
    }
  }
  .close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 16px;
    color: #aaa;
  }
  .btn-box {
    margin-top: 16px;
    padding: 0 10px;
    .register {
      margin-top: 20px;
    }
  }
  .login-good {
    margin: 40px auto;
    width: 80px;
    .luckc-logo {
      display: inline-block;
    }
    .good-text {
      font-size: 30px;
      margin-top: 10px;
      color: #1296db;
      font-weight: 700;
    }
    .good-entext {
      font-size: 18px;
      color: #1296db;
      margin-top: 20px;
    }
  }
  .pwd {
    text-align: right;
    padding-right: 20px;
  }
}
</style>